.card {
  padding: 20px;
  transition: all .5s ease-in-out;
  box-shadow: 1px 1px 5px #0000004d;
}

.card:hover {
  transform: scale(1.05);
}

.services-card {
  border: 2px solid #000;
  border-radius: 15px;
  margin-bottom: 25px;
  padding: 20px;
  transition: all .3s ease-in-out;
}

.link-icon {
  color: #ff601c;
  margin-bottom: 25px;
  position: absolute;
  bottom: 0;
}

.project-card {
  background-color: #fff;
  flex-wrap: wrap;
  align-items: center;
  gap: 10px;
  margin-bottom: 25px;
  padding: 20px;
  transition: all .3s ease-in-out;
  position: relative;
  box-shadow: 1px 1px 5px #0000004d;
}

.project-card p {
  margin-top: 10px;
  margin-bottom: 50px;
