.project-card h2 {
  width: 100%;
  margin-top: 10px;
  font-size: 19px;
}

.project-card:hover {
  transform: scale(1.02);
}

#contact-card {
  background-color: #8b93f5;
  border: 1px solid #ff601c;
  border-radius: 10px;
  margin-top: 25px;
  margin-bottom: 50px;
  padding: 25px;
}

#contact-card h2 {
  font-size: 22px;
  font-weight: 400;
}

#contact-card p {
  font-size: 18px;
}

#demo-reel-card {
  margin-top: 25px;
  display: grid;
}

#demo-reel-card h2 {
  width: 100%;
