}

@media screen and (width >= 768px) {
  .project-card {
    background-color: #8b93f5;
  }

  .project-card:hover {
    background-color: #fff;
  }
}

@media screen and (width >= 1200px) {
  .services-card h2 {
    font-size: 22px;
    font-weight: 400;
  }

  .services-card:hover {
    background-color: #e6e6e6;
    border-color: #ff601c;
    border-bottom-width: 10px;
  }

  #contact-card {
    border-bottom-width: 10px;
  }

  #contact-form {
    margin-top: 25px;
  }

  #demo-reel-card {
    margin-top: 25px;
    display: grid;
  }

  #demo-reel-card h2 {
    width: 100%;
    margin: auto;
    padding-bottom: 0;
    font-size: 26px;
    font-weight: 400;
  }

  #demo-reel-card p {
    padding-right: 50px;
    font-size: 18px;
    line-height: 1.5em;
  }
}

@media screen and (width >= 1920px) {
  .project-card h2 {
    font-size: 22px;
  }

  .project-card p {
    font-size: 20px;
    line-height: 2rem;
  }

  #contact-card h2 {
    font-size: 32px;
  }

  #contact-card p {
    font-size: 22px;
  }

  #demo-reel-card {
    border-radius: 10px;
    margin-top: 25px;
    display: grid;
  }

  #demo-reel-card h2 {
    width: 100%;
    margin: auto;
    font-size: 32px;
    font-weight: 400;
  }

  #demo-reel-card p {
    padding-right: 50px;
    font-size: 22px;
    line-height: 2em;
  }
}

